body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body,
body * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  vertical-align: top; }

body {
  background-color: #282E33; }

.app__wrapper {
  display: block;
  width: 1280px;
  height: 600px;
  margin-top: 60px;
  margin-left: calc((100% / 2) - 640px);
  background: #D6D8C9; }

.content__wrapper {
  display: inline-block;
  float: left;
  width: 900px;
  height: 600px; }
  .content__wrapper .search {
    height: 80px;
    padding: 20px; }
    .content__wrapper .search form input {
      width: 100%;
      height: 40px;
      padding: 0 20px;
      border: 0px;
      border-bottom: 1px solid black;
      font-size: 14px;
      background-color: rgba(0, 0, 0, 0.1); }
    .content__wrapper .search form input:hover {
      background-color: #EFF2E2; }
  .content__wrapper .gallery {
    display: grid;
    height: 500px;
    padding: 0px 20px 0px 20px;
    margin: 0px 20px;
    background-color: #EFF2E2;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(140px, auto));
    grid-auto-flow: dense;
    grid-auto-rows: 180px;
    overflow: scroll; }
    .content__wrapper .gallery .img__horizontal {
      grid-column: span 2;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover; }
    .content__wrapper .gallery .img__vertical {
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover; }

.sidebar__wrapper {
  display: inline-block;
  float: right;
  width: 380px;
  height: 600px; }
  .sidebar__wrapper .buttons {
    height: 80px;
    padding: 20px; }
    .sidebar__wrapper .buttons .buttons__btn {
      display: inline-block;
      width: calc(50% - 20px);
      height: 40px;
      border-bottom: 1px solid black;
      background-color: rgba(0, 0, 0, 0.1);
      line-height: 40px;
      text-align: center; }
    .sidebar__wrapper .buttons .buttons__btn:hover {
      background-color: #EFF2E2; }
    .sidebar__wrapper .buttons .buttons__btn:last-child {
      float: right; }
  .sidebar__wrapper .queries {
    display: block;
    height: 500px;
    padding: 0px 20px 0px 20px;
    margin: 0px 20px;
    background-color: #EFF2E2;
    overflow: scroll; }
    .sidebar__wrapper .queries ol li {
      padding: 5px;
      margin: 0px 20px; }

